.pokemon {
  border-radius: 10px;
  bottom: 36.6rem;
  display: flex;
  position: absolute;
  width: 50%;
}

.pokemon > img {
  height: 16.3rem;
  left: 4rem;
  position: relative;
  width: 16.3rem;
}

.pokemon p {
  color: rgb(115, 255, 0);
  font-size: 1.8rem;
}

.info-container, .type-container {
  position: absolute;
}

.info-container {
  bottom: 8rem;
  left: 40rem;
}

.type-container {
  align-items: center;
  border-radius: 0.4rem;
  bottom: -17.5rem;
  display: flex;
  height: 3.6rem;
  justify-content: center;
  left: 38.9rem;
  text-align: center;
  width: 11.2rem;
}

.pokemon .Electric {
  background-color: rgb(238, 213, 53);
  color: black;
}

.pokemon .Fire {
  background-color: rgb(253, 125, 36);
  color: white;
}

.pokemon .Bug {
  background-color: rgb(114, 159, 63);
  color: white;
}

.pokemon .Poison {
  background-color: rgb(185, 127, 201);
  color: white;
}

.pokemon .Psychic {
  background-color: rgb(243, 102, 185);
  color: white;
}

.pokemon .Normal {
  background-color: rgb(164, 172, 175);
  color: black;
}

.pokemon .Dragon {
  background-image: linear-gradient(rgb(83, 164, 207) 50%, rgb(241, 110, 87) 50%);
  color: white;
}

@media ( max-width : 1100px ) {

  .pokemon > img {
    bottom: -7.2rem;
    height: 13rem;
    left: 2.8rem;
    width: 13rem;
  }

  .pokemon p {
    font-size: 1.6rem;
  }

  .info-container {
    bottom: -0.9rem;
    left: 31.5rem;
  }

  .type-container {
    bottom: -21.3rem;
    height: 2.9rem;
    left: 31.1rem;
    width: 8.9rem;
  }
}

@media ( max-width : 800px ) {

  .pokemon > img {
    bottom: -13.2rem;
    height: 10.5rem;
    left: 2.3rem;
    width: 10.5rem;
  }

  .pokemon p {
    font-size: 1.2rem;
  }

  .info-container {
    bottom: -7.8rem;
    left: 25.5rem;
  }

  .type-container {
    bottom: -24.4rem;
    height: 2.4rem;
    left: 24.9rem;
    width: 7rem;
  }
}

@media ( max-width : 600px ) {

  .pokemon > img {
    bottom: -19.1rem;
    height: 7.8rem;
    left: 1.8rem;
    width: 7.8rem;
  }

  .pokemon p {
    font-size: 0.9rem;
  }

  .info-container {
    bottom: -15rem;
    left: 19.1rem;
  }

  .type-container {
    bottom: -27.4rem;
    height: 1.8rem;
    left: 18.7rem;
    width: 5.3rem;
  }
}

@media ( max-width : 450px ) {

  .pokemon > img {
    bottom: -20.1rem;
    height: 7.3rem;
    left: 1.5rem;
    width: 7.3rem;
  }

  .pokemon p {
    font-size: 0.8rem;
  }

  .info-container {
    bottom: -16.4rem;
    left: 17.9rem;
  }

  .type-container {
    bottom: -28rem;
    height: 1.6rem;
    left: 17.4rem;
    width: 4.9rem;
  }
}
