.pokedex {
  align-items: center;
  bottom: 4.2rem;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: flex-end;
  max-height: calc(100vh - 17.2rem);
  position: fixed;
  width: fit-content;
  z-index: 1;
}

.pokedex > img {
  max-width: 125rem;
}

.filter-buttons {
  bottom: 33.6rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  left: 70.4rem;
  position: absolute;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  height: 3.5rem;
  width: 6.15rem;
}

button > svg {
  height: 3rem;
  width: 3rem;
}

.change-pokemon > button {
  font-size: 0;
  width: 4.9rem;
}

.change-pokemon {
  bottom: 26.6rem;
  display: flex;
  left: 70.4rem;
  position: absolute;
}

@media ( max-width : 1100px ) {

  .pokedex > img {
    max-width: 100rem;
  }

  .filter-buttons {
    bottom: 26.8rem;
    left: 56.3rem;
  }

  button {
    height: 2.8rem;
    width: 4.9rem;
  }

  button > svg {
    height: 2.5rem;
    width: 2.5rem;
  }

  .change-pokemon > button {
    width: 4rem;
  }

  .change-pokemon {
    bottom: 21.2rem;
    display: flex;
    left: 56.3rem;
    position: absolute;
  }
}

@media ( max-width : 800px ) {

  .pokedex > img {
    max-width: 80rem;
  }

  .filter-buttons {
    bottom: 21.5rem;
    left: 45.1rem;
  }

  button {
    height: 2.2rem;
    width: 3.95rem;
  }

  .filter-buttons > button {
    font-size: 1rem;
  }


  button > svg {
    height: 2rem;
    width: 2rem;
  }

  .change-pokemon > button {
    width: 3.2rem;
  }

  .change-pokemon {
    bottom: 17rem;
    display: flex;
    left: 45rem;
    position: absolute;
  }
}

@media ( max-width : 600px ) {

  .pokedex > img {
    max-width: 60rem;
  }

  .filter-buttons {
    bottom: 16.1rem;
    left: 33.9rem;
  }

  button {
    height: 1.7rem;
    width: 2.95rem;
  }

  .filter-buttons > button {
    font-size: 0.7rem;
  }


  button > svg {
    height: 1.5rem;
    width: 1.5rem;
  }

  .change-pokemon > button {
    width: 2.4rem;
  }

  .change-pokemon {
    bottom: 12.7rem;
    display: flex;
    left: 33.8rem;
    position: absolute;
  }
}

@media ( max-width : 450px ) {

  .pokedex > img {
    max-width: 56rem;
  }

  .filter-buttons {
    bottom: 15rem;
    left: 31.5rem;
  }

  button {
    height: 1.58rem;
    width: 2.78rem;
  }

  .change-pokemon > button {
    width: 2.3rem;
  }

  .change-pokemon {
    bottom: 11.9rem;
    display: flex;
    left: 31.5rem;
    position: absolute;
  }
}
